import React from 'react'
import { Link } from 'react-router-dom'
import ScrollAnimation from 'react-animate-on-scroll';

import '../../assets/css/animate.min.css'
import '../../assets/css/contactus.css'

import Footer from '../Layout/footer'
import useStore from '../../useStore'
// import { tips, validateEmail, validateUsername } from '../../util'
import SelectInput from '../../components/SelectInput'

import Carousel from '../../components/Carousel'
import SideMenu from '../Layout/sidemenu'
import '../../components/carousel.css'

import logo from '../../assets/img/logo_white.svg'
import menu from '../../assets/img/menu.webp'
import header_phone from '../../assets/img/phone.svg'
import phone from '../../assets/img/phone-red.svg'
import password from '../../assets/img/password.webp'
import home from '../../assets/img/home.webp';
import header_back from '../../assets/img/header_back.webp';
import book from '../../assets/img/book.svg';
import lock from '../../assets/img/lock.svg';
import question from '../../assets/img/question.svg';
import map from '../../assets/img/map.webp';

import skype from '../../assets/img/skype.webp';
import meet from '../../assets/img/meet.webp';
import skype2 from '../../assets/img/skype2.webp';
import zoom from '../../assets/img/zoom.webp';
import teams from '../../assets/img/teams.webp';
import flag from '../../assets/img/flag.svg';


interface MsgType {
	email: string
	name: string
	subject: string
	departnemt: number
	msg: string
	meeting: number
	accept: boolean
	checkbox1: boolean
	checkbox2: boolean
	checkbox3: boolean
	checkbox4: boolean
	checkbox5: boolean
	showSideMenu: boolean
}

const Infrastructure = () => {
	const [state, setStates] = React.useState<MsgType>({
		email: '',
		name: '',
		subject: '',
		departnemt: 0,
		msg: '',
		meeting: 0,
		accept: false,
		checkbox1: true,
		checkbox2: false,
		checkbox3: false,
		checkbox4: false,
		checkbox5: false,
		showSideMenu: false
	})
	const { T, changeLang, lang } = useStore()
	const updateStatus = (params: { [key: string]: string | number | boolean }) => setStates({ ...state, ...params })



	const items = ['one', 'two', 'three', 'four', 'five']

	const setting = {
		dragSpeed: 1.25,
		itemWidth: 300,
		itemSideOffsets: 15,
	}


	// const submit = async () => {
	// 	const { email } = state
	// 	if (email === '' || !validateEmail(email)) {
	// 		tips("Invalid e-mail")
	// 		return
	// 	}
	// 	update({ loading: true })
	// 	// const res = await call("submit", { email, password });
	// 	// if (res) {
	// 	tips("Success")
	// 	// }
	// 	// update({ loading: false })
	// 	setTimeout(() => {
	// 		update({ loading: false })
	// 	}, 2000);
	// }

	React.useEffect(() => {
		window.scrollTo(0, 0)
	}, [])

	return (
		<div className="contactus">
			<div className="header-panel" style={{ backgroundColor: 'rgba(0,0,0,0.7)', position: 'relative', backgroundImage: `url(${header_back})` }}>
				<div className="container-sm">
					<header>
						<div className='justify'>
							<div className='justify'>
								<Link to="/"><img src={logo} className="logo " alt={'logo'} /></Link>
								<div style={{ width: '140px' }} >
									<SelectInput name="Select State" type="language" valueIndex={lang} items={["English", "Arabic", "Chinese"]} onValueChange={(value) => { changeLang(value) }} />
								</div>
							</div>
							<div className='header-menu-bar ml5'>
								<Link to="/solutions" className="header-menu"><span></span>{T("menu.solutions")}</Link>
								<Link to="/infrastructure" className="header-menu "><span></span>{T("menu.infrastructure")}</Link>
								<Link to="/hosting" className="header-menu "><span></span>{T("menu.hosting")}</Link>
								<Link to="/contactus" className="header-menu active"><span></span>{T("menu.support")}</Link>
							</div>
							<div></div>
							<div className='justify'>
								<a href="tel:08002289090" className='header-phone justify' >
									<img src={header_phone} className="mr2  header-menu-icon" alt={'phone'} />
									<h4 className='text-light'>800 228 9090</h4>
								</a>
								<Link to="/login" className='btn-login btn-yo'>{T("login.btn.login")}</Link>
								<Link to="/"><img src={password} className="btn-login header-menu-icon" alt={'login'} /></Link>
								<span style={{ cursor: 'pointer' }} onClick={() => { updateStatus({ showSideMenu: true }) }}><img src={menu} className="header-menu-icon" alt={'menu'} /></span>
							</div>
						</div>
					</header>
					<hr style={{ borderTop: '1px solid var(--font-black-color)' }} />
					<div className="container-sm">
						<div className="row">
							<div className="col-12 ">
								<h3 className='text-stroke h2 title' style={{ zIndex: 0, WebkitTextStroke: '1px rgba(255,255,255,0.1)', top: '-20%' }}>{T("menu.contactus")}</h3>
								<h1 className='text-light h1 title' style={{ zIndex: 2 }}>{T("menu.contactus")}</h1>
							</div>
						</div>
						<div className="row m0 pb3">
							<div className="col-12" style={{ fontSize: '24px' }}>
								<Link to="/"><img src={home} style={{ width: '20px' }} alt={'home'} /></Link>
								<label className='text-light'> / </label>
								<Link to="/"><label className='text-primary'> {T("menu.contactus")} </label></Link>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className="section-1">
				<div className="container-sm">
					<div className="row center">
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<h2 className='text-primary m0 p0'>{T("contact.title")}</h2>
						</ScrollAnimation>
					</div>
					<div className="row center">
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<h4 className='text-dark m0 p0'>{T("contact.desc")}</h4>
						</ScrollAnimation>
					</div>
				</div>
				<div className="container">

					<div className="row center mt5">
						<div className="col-xl-3 col-lg-4 col-md-6 p0">
							<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
								<a href="tel:08002289090"  >
									<div className='contact-card' >
										<img className='contact-type' src={phone} alt="phone"></img>
										<h4 className='text-primary p0 m0'>{T("contact.card1.title")}</h4>
										<h5 className='text-dark p1 m0'>{T("contact.card1.desc")}</h5>
									</div>
								</a>
							</ScrollAnimation>
						</div>
						<div className="col-xl-3 col-lg-4 col-md-6 p0">
							<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
								<Link to="/knowledge">
									<div className='contact-card' >
										<img className='contact-type' src={book} alt="contact"></img>
										<h4 className='text-primary p0 m0'>{T("contact.card2.title")}</h4>
										<h5 className='text-dark p1 m0'>{T("contact.card2.desc")}</h5>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
						<div className="col-xl-3 col-lg-4 col-md-6 p0">
							<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
								<a href="https://hosting.yonescat.com/">
									<div className='contact-card' >
										<img className='contact-type' src={lock} alt="lock"></img>
										<h4 className='text-primary p0 m0'>{T("contact.card3.title")}</h4>
										<h5 className='text-dark p1 m0'>{T("contact.card3.desc")}</h5>
									</div>
								</a>
							</ScrollAnimation>
						</div>
						<div className="col-xl-3 col-lg-4 col-md-6 p0">
							<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
								<Link to="faq">
									<div className='contact-card' >
										<img className='contact-type' src={question} alt="question"></img>
										<h4 className='text-primary p0 m0'>{T("contact.card4.title")}</h4>
										<h5 className='text-dark p1 m0'>{T("contact.card4.desc")}</h5>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
					</div>
				</div>
			</div>
			<div className="section-2">
				<div className="container">
					<ScrollAnimation animateIn="fadeIn" animateOut="fadeOutUp">
						<div className="row">
							<div className="col-xl-6 col-md-12">
								<div className="row ">
									<div className="col-12" style={{ position: 'relative' }}>
										<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
											<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
												<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("contact.sec2.title")}</h3>
											</ScrollAnimation>
										</ScrollAnimation>
									</div>
								</div>
								<div className="row">
									<h4 className='text-dark pl3'>{T("contact.sec2.desc")}</h4>
								</div>
								<div className="row">
									<div className="col-xl-6 col-md-12">
										<input type="text" className='w100' placeholder={T('contact.sec2.name')} />
									</div>

									<div className="col-xl-6 col-md-12">
										<input type="text" className='w100' placeholder={T('contact.sec2.email')} />
									</div>
								</div>
								<div className="row">
									<div className="col-xl-6 col-md-12">
										<input type="text" className='w100' placeholder={T('contact.sec2.subject')} />
									</div>
									<div className="col-xl-6 col-md-12">
										<SelectInput name={T("contact.sec2.department")} type="" valueIndex={lang} items={["A", "B", "C"]} onValueChange={() => { }} />
									</div>
								</div>
								<div className="row">
									<div className="col-12">
										<textarea className='w100 ' rows={5} placeholder={T("contact.sec2.message")} />
									</div>
								</div>
								<div className="row">
									<div className="col-12">
										<p className='text-dark'>Select mode of meeting</p>
									</div>
								</div>
								<div className="row">
									<div className="col-xl-2 col-lg-6 col-md-12">
										<div className='radio-label' onClick={() => updateStatus({ checkbox1: !state.checkbox1 })}><span className={`radio ${state.checkbox1 ? "checked" : ""}`}></span>Skype</div>
									</div>
									<div className="col-xl-4 col-lg-6 col-md-12">
										<div className='radio-label' onClick={() => updateStatus({ checkbox2: !state.checkbox2 })}><span className={`radio ${state.checkbox2 ? "checked" : ""}`}></span>GoogleMeeting</div>
									</div>
									<div className="col-xl-3 col-lg-6 col-md-12">
										<div className='radio-label' onClick={() => updateStatus({ checkbox3: !state.checkbox3 })}><span className={`radio ${state.checkbox3 ? "checked" : ""}`}></span>Zoom</div>
									</div>
									<div className="col-xl-3 col-lg-6 col-md-12">
										<div className='radio-label' onClick={() => updateStatus({ checkbox4: !state.checkbox4 })}><span className={`radio ${state.checkbox4 ? "checked" : ""}`}></span>WhatApp</div>
									</div>
								</div>
								<div className="row mt3">
									<div className="col-12">
										<div className='radio-label' onClick={() => updateStatus({ checkbox5: !state.checkbox5 })}><span className={`radio ${state.checkbox5 ? "checked" : ""}`}></span>{T("contact.sec2.accept")}</div>
									</div>
								</div>
								<div className="row mt3">
									<button className='btn-yo'>{T("contact.submit")}</button>
									<button className='btn-white'>{T("contact.reset")}</button>
								</div>
							</div>
						</div>
					</ScrollAnimation>
				</div>
			</div>
			<div className="section-3">
				<div className="row center">
					<div className="col-12" style={{ position: 'relative' }}>
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<h3 className='h2 text-stroke' style={{ top: '-100%', left: 'calc(50% - 12rem)' }}>{T("contact.locate")}</h3>
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("contact.locate")}</h3>
							</ScrollAnimation>
						</ScrollAnimation>
					</div>
				</div>
				<div className="row center">
					<ScrollAnimation animateIn="fadeIn" animateOut="fadeOutUp">
						<img src={map} alt="our location" />
					</ScrollAnimation>
				</div>
			</div>
			<div className="section-4">
				<div className="row center">
					<div className="col-12" style={{ position: 'relative' }}>
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<h3 className='h2 text-stroke' style={{ left: 'calc(50% - 12rem)', top: '-120%' }}>{T("contact.sec3.title")}</h3>
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("contact.sec3.title")}</h3>
							</ScrollAnimation>
						</ScrollAnimation>
					</div>
				</div>
				<div className="container">
					<div className="row middle">
						<div className="col-xl-4 col-lg-6 col-md-12">
							<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
								<Link to="/">
									<div className="connect-card">
										<div className="row">
											<div className="col-12">
												<img src={teams} alt="teams-icon" style={{ width: '100px' }} />
											</div>
										</div>
										<div className="row">
											<div className="col-12">
												<h3 className='text-dark'>Microsoft Teams</h3>
											</div>
										</div>
										<div className="row">
											<div className="col-12">
												<h4 className="text-primary m0 p0">10:00 AM - 05:00 PM</h4>
												<h4 className="text-primary m0 p0">{`${T("contact.monday")} - ${T("contact.friday")}`}</h4>
											</div>
										</div>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
						<div className="col-xl-8 col-lg-6 col-md-12">
							<div className="row">
								<div className="col-xl-6 col-lg-12">
									<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
										<Link to="/">
											<div className="connect-card m0 p1">
												<div className="row middle">
													<div className="col-xl-4 col-md-12">
														<img src={skype} alt="teams-icon" style={{ width: '60px' }} />
													</div>
													<div className="col-xl-8 col-md-12">
														<h4 className='text-dark m0 p0'>Skype</h4>
														<p className='text-dark m0 p0'>yonescatOnline</p>
													</div>
												</div>
												<div className="row">
													<div className="col-xl-4 col-md-12"></div>
													<div className="col-xl-8 col-md-12">
														<p className="text-primary m0 p0">10:00 AM - 05:00 PM</p>
														<p className="text-primary m0 p0">{`${T("contact.monday")} - ${T("contact.friday")}`}</p>
													</div>
												</div>
											</div>
										</Link>
									</ScrollAnimation>
								</div>
								<div className="col-xl-6 col-lg-12">
									<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
										<Link to="/">
											<div className="connect-card m0">
												<div className="row middle">
													<div className="col-12">
														<img src={zoom} alt="zoom-icon" style={{ width: '50%' }} /></div>
												</div>
												<div className="row">
													<div className="col-12">
														<h4 className='text-dark m0 p0'>Zoom Call</h4></div>
												</div>
												<div className="row">
													<div className="col-12">
														<p className="text-primary m0 p0">10:00 AM - 05:00 PM</p>
														<p className="text-primary m0 p0">{`${T("contact.monday")} - ${T("contact.friday")}`}</p>
													</div>
												</div>
											</div>
										</Link>
									</ScrollAnimation>
								</div>
								<div className="col-xl-6 col-lg-12">
									<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
										<Link to="/">
											<div className="connect-card m0 p1">
												<div className="row middle">
													<div className="col-xl-4 col-md-12">
														<img src={skype2} alt="teams-icon" style={{ width: '60px' }} />
													</div>
													<div className="col-xl-8 col-md-12">
														<h4 className='text-dark m0 p0'>Whatsapp</h4>
														<p className='text-dark m0 p0'>+123 456789</p>
													</div>
												</div>
												<div className="row">
													<div className="col-xl-4 col-md-12"></div>
													<div className="col-xl-8 col-md-12">
														<p className="text-primary m0 p0">10:00 AM - 05:00 PM</p>
														<p className="text-primary m0 p0">{`${T("contact.monday")} - ${T("contact.friday")}`}</p>
													</div>
												</div>
											</div>
										</Link>
									</ScrollAnimation>
								</div>
								<div className="col-xl-6 col-lg-12">
									<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
										<Link to="/">
											<div className="connect-card m0 p1">
												<div className="row middle">
													<div className="col-xl-4 col-md-12">
														<img src={meet} alt="teams-icon" style={{ width: '60px' }} />
													</div>
													<div className="col-xl-8 col-md-12">
														<h4 className='text-dark m0 p0'>Google Meet</h4>
													</div>
												</div>
												<div className="row">
													<div className="col-xl-4 col-md-12"></div>
													<div className="col-xl-8 col-md-12">
														<p className="text-primary m0 p0">10:00 AM - 05:00 PM</p>
														<p className="text-primary m0 p0">{`${T("contact.monday")} - ${T("contact.friday")}`}</p>
													</div>
												</div>
											</div>
										</Link>
									</ScrollAnimation>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className="section-5">
				<div className="container">
					<div className="row center">
						<div className="col-12" style={{ position: 'relative' }}>
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<h3 className='h2 text-stroke' style={{ left: 'calc(50% - 12rem)', top: '-120%' }}>{T("contact.sec4.title")}</h3>
								<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
									<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("contact.sec4.title")}</h3>
								</ScrollAnimation>
							</ScrollAnimation>
						</div>
					</div>
					<Carousel _data={items} {...setting}>
						<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
							<a href="tel:08002289090">
								<div className="soft-card">
									<img src={flag} alt="flag" />
									<h3>{T("contact.london")}</h3>
									<p>{T("contact.callus")}:<b className='text-primary'>+44 (0) 800 228 9090</b></p>
									<p>{T("contact.callus_desc")}</p>
								</div>
							</a>
						</ScrollAnimation>
						<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
							<a href="tel:08002289090">
								<div className="soft-card active" >
									<img src={flag} alt="flag" />
									<h3>{T("contact.london")}</h3>
									<p>{T("contact.callus")}:<b className='text-primary'>+44 (0) 800 228 9090</b></p>
									<p>{T("contact.callus_desc")}</p>
								</div>
							</a>
						</ScrollAnimation>
						<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
							<a href="tel:08002289090">
								<div className="soft-card">
									<img src={flag} alt="flag" />
									<h3>{T("contact.london")}</h3>
									<p>{T("contact.callus")}:<b className='text-primary'>+44 (0) 800 228 9090</b></p>
									<p>{T("contact.callus_desc")}</p>
								</div>
							</a>
						</ScrollAnimation>

						<ScrollAnimation animateIn="flipInY" animateOut="fadeOutUp">
							<a href="tel:08002289090">
								<div className="soft-card active">
									<img src={flag} alt="flag" />
									<h3>{T("contact.london")}</h3>
									<p>{T("contact.callus")}:<b className='text-primary'>+44 (0) 800 228 9090</b></p>
									<p>{T("contact.callus_desc")}</p>
								</div>
							</a>
						</ScrollAnimation>
					</Carousel>
				</div>
			</div>
			<Footer />
			<SideMenu menushow={state.showSideMenu} onClose={() => updateStatus({ showSideMenu: false })} />
			<div>
				{/* <img src={ellipse1} className="ellipse ellipse1" alt='' /> */}
				{/* <img src={ellipse3} className="ellipse ellipse3" alt='' /> */}
				{/* <img src={ellipse2} className="ellipse ellipse2" alt='' /> */}
			</div>
		</div >
	)
};

export default Infrastructure;