import React from 'react'
import { Link } from 'react-router-dom'
import ScrollAnimation from 'react-animate-on-scroll';

import '../../assets/css/animate.min.css'
import '../../assets/css/infrastructure.css'

import Footer from '../Layout/footer'
import useStore from '../../useStore'
import { tips, validateEmail } from '../../util'
import SelectInput from '../../components/SelectInput'
import Carousel from '../../components/Carousel'
import SideMenu from '../Layout/sidemenu'
import '../../components/carousel.css'

import logo from '../../assets/img/logo_white.svg'
import menu from '../../assets/img/menu.webp'
import phone from '../../assets/img/phone.svg'
import password from '../../assets/img/password.webp'
import home from '../../assets/img/home.webp';
import arrow_left from '../../assets/img/arrow_left.svg'
import header_back from '../../assets/img/header_back.webp';


import data_center from '../../assets/img/data_center.svg';
import db_service from '../../assets/img/db-as-service.svg';
import DDos from '../../assets/img/DDos.svg';
import G_suite from '../../assets/img/G-suite.svg';
import office from '../../assets/img/office365.webp';
import voip from '../../assets/img/voip.svg';

import smile from '../../assets/img/smile-o.svg';
import code from '../../assets/img/code.webp';
import coffee from '../../assets/img/coffee.webp';
import trophy from '../../assets/img/trophy.webp';


import ellipse1 from '../../assets/img/Ellipse_1.webp'
import ellipse2 from '../../assets/img/Ellipse_2.webp'
import ellipse3 from '../../assets/img/Ellipse_3.webp'

interface MsgType {
	email: string
	showSideMenu: boolean
}

const Infrastructure = () => {
	const [state, setStates] = React.useState<MsgType>({
		email: '',
		showSideMenu: false
	})
	const { update, T, changeLang, lang } = useStore()
	const updateStatus = (params: { [key: string]: string | number | boolean }) => setStates({ ...state, ...params })

	React.useEffect(() => {
		window.scrollTo(0, 0)
	}, [])

	const submit = async () => {
		const { email } = state
		if (email === '' || !validateEmail(email)) {
			tips("Invalid e-mail")
			return
		}
		update({ loading: true })
		// const res = await call("submit", { email, password });
		// if (res) {
		tips("Success")
		// }
		// update({ loading: false })
		setTimeout(() => {
			update({ loading: false })
		}, 2000);
	}


	const items = ['one', 'two', 'three', 'four', 'five']

	const setting = {
		dragSpeed: 1.25,
		itemWidth: 300,
		itemSideOffsets: 15,
	}


	return (
		<div className="infrastructure">
			<div className="header-panel" style={{ backgroundColor: 'rgba(0,0,0,0.7)', position: 'relative', backgroundImage: `url(${header_back})` }}>
				<div className="container">
					<header>
						<div className='justify'>
							<div className='justify'>
								<Link to="/"><img src={logo} className="logo " alt={'logo'} /></Link>
								<div style={{ width: '140px' }} >
									<SelectInput name="Select State" type="language" valueIndex={lang} items={["English", "Arabic", "Chinese"]} onValueChange={(value) => { changeLang(value) }} />
								</div>
							</div>
							<div className='header-menu-bar ml5'>
								<Link to="/solutions" className="header-menu"><span></span>{T("menu.solutions")}</Link>
								<Link to="/infrastructure" className="header-menu active"><span></span>{T("menu.infrastructure")}</Link>
								<Link to="/hosting" className="header-menu"><span></span>{T("menu.hosting")}</Link>
								<Link to="/contactus" className="header-menu"><span></span>{T("menu.support")}</Link>
							</div>
							<div></div>
							<div className='justify'>
								<a href="tel:08002289090" className='header-phone justify' >
									<img src={phone} className="mr2  header-menu-icon" alt={'phone'} />
									<h4 className='text-light'>800 228 9090</h4>
								</a>
								<Link to="/login" className='btn-login btn-yo'>{T("login.btn.login")}</Link>
								<Link to="/"><img src={password} className="btn-login header-menu-icon" alt={'login'} /></Link>
								<span style={{ cursor: 'pointer' }} onClick={() => { updateStatus({ showSideMenu: true }) }}><img src={menu} className="header-menu-icon" alt={'menu'} /></span>
							</div>
						</div>
					</header>
					<hr style={{ borderTop: '1px solid var(--font-black-color)' }} />
					<div className="container">
						<div className="row">
							<div className="col-12 ">
								<h3 className='text-stroke h2 title' style={{ zIndex: 0, WebkitTextStroke: '1px rgba(255,255,255,0.1)' }}>{T("menu.solutions")}</h3>
								<h1 className='text-light h1 title' style={{ zIndex: 2 }}>{T("menu.solutions")}</h1>
							</div>
						</div>
						<div className="row m0 pb3">
							<div className="col-12" style={{ fontSize: '24px' }}>
								<Link to="/"><img src={home} style={{ width: '20px' }} alt={'home'} /></Link>
								<label className='text-light'> / </label>
								<Link to="/"><label className='text-primary'> {T("menu.solutions")} </label></Link>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className="section-1">
				<div className="container">
					<div className="row mt3" style={{ alignItems: 'center' }}>
						<div className="col-xl-7 col-md-12">
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<div className="row center">
									<h2 className='text-light p3' style={{ width: '80%' }}>{T("solutions.desc1")}</h2>
								</div>
							</ScrollAnimation>
						</div>
						<div className="col-xl-5 col-md-12">
							<div className="row center">
								<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
									<div className="row"><h5>{T("solutions.desc2")}</h5></div>
								</ScrollAnimation>
							</div>
							<div className="row center">
								<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
									<Link to="contactus" className='btn-transparent'>{T("btn.contact")}</Link>
								</ScrollAnimation>
							</div>

						</div>
					</div>
					<ScrollAnimation animateIn="fadeInRight" animateOut="fadeOutUp">
						<Carousel _data={items} {...setting}>
							<div className="soft-card" style={{ paddingLeft: 0, paddingRight: 0 }}>
								<img src={data_center} alt="desktop" style={{ width: '50%' }} />
								<h4>{T("infrastructure.card.title7")}</h4>
								<Link to="/infrastructure-details/iptv" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
							<div className="soft-card" style={{ paddingLeft: 0, paddingRight: 0 }}>
								<img src={voip} alt="desktop" style={{ width: '50%' }} />
								<h4>{T("infrastructure.card.title6")}</h4>
								<Link to="/infrastructure-details/voip" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
							<div className="soft-card">
								<img src={G_suite} alt="desktop" />
								<h4>{T("infrastructure.card.title5")}</h4>
								<Link to="/infrastructure-details/g-suite" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
							<div className="soft-card">
								<img src={office} alt="desktop" />
								<h4>{T("infrastructure.card.title4")}</h4>
								<Link to="/infrastructure-details/office" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
							<div className="soft-card">
								<img src={db_service} alt="desktop" />
								<h4>{T("infrastructure.card.title3")}</h4>
								<Link to="/infrastructure-details/database" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
							<div className="soft-card">
								<img src={DDos} alt="desktop" />
								<h4>{T("infrastructure.card.title2")}</h4>
								<Link to="/infrastructure-details/ddos" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
							<div className="soft-card">
								<img src={data_center} alt="desktop" />
								<h4>{T("infrastructure.card.title1")}</h4>
								<Link to="/infrastructure-details/data" className='center  h4'><img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-25px)', width: '30px' }} alt="arrow-left" />{T("title.learn_more")}</Link>
							</div>
						</Carousel>
					</ScrollAnimation>
					<div className="row mt2 center">
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<button className='btn-transparent'>{T("btn.load_more")}</button>
						</ScrollAnimation>
					</div>
				</div>
			</div>
			<div className="section-2">
				<div className="container">
					<div className="row">
						<div className="col-12" style={{ position: 'relative' }}>
							<h3 className='h2 text-stroke' style={{ left: 'calc(50% - 8rem)' }}>{T("title.fun_fact")}</h3>
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("title.fun_fact")}</h3>
							</ScrollAnimation>
						</div>
					</div>
					<div className="row center">
						<div className="col-lg-3 col-md-6">
							<ScrollAnimation animateIn="slideInUp" animateOut="fadeOutUp">
								<div className="fact-card">
									<h3>500</h3>
									<p className='text-primary'>{T("infrastructure.fact.title1")}</p>
									<img src={smile} alt="desktop" />
								</div>
							</ScrollAnimation>
						</div>
						<div className="col-lg-3 col-md-6">
							<ScrollAnimation animateIn="slideInUp" animateOut="fadeOutUp">
								<div className="fact-card">
									<h3>125</h3>
									<p className='text-primary'>{T("infrastructure.fact.title2")}</p>
									<img src={code} alt="desktop" />
								</div>
							</ScrollAnimation>
						</div>
						<div className="col-lg-3 col-md-6">
							<ScrollAnimation animateIn="slideInUp" animateOut="fadeOutUp">
								<div className="fact-card">
									<h3>250k</h3>
									<p className='text-primary'>{T("infrastructure.fact.title3")}</p>
									<img src={coffee} alt="desktop" />
								</div>
							</ScrollAnimation>
						</div>
						<div className="col-lg-3 col-md-6">
							<ScrollAnimation animateIn="slideInUp" animateOut="fadeOutUp">
								<div className="fact-card">
									<h3>10</h3>
									<p className='text-primary'>{T("infrastructure.fact.title4")}</p>
									<img src={trophy} alt="desktop" />
								</div>
							</ScrollAnimation>
						</div>
					</div>
				</div>
			</div>
			<div className="section-3">
				<div className="container">
					<div className="row center justify">
						<div className="col-12" style={{ position: 'relative' }}>
							<h3 className='h2 text-stroke' style={{ left: 'calc(50% - 8rem)' }}>{T("title.newsletter")}</h3>
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("title.newsletter")}</h3>
							</ScrollAnimation>
						</div>
					</div>
					<div className="row center">
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<h3>{T("solutions.newsletter.desc")}</h3>
						</ScrollAnimation>
					</div>
					<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
						<div className="row center">
							<div className="col-3"></div>
							<div className="col-xl-6 col-md-12 row">
								<input type="text" placeholder={T("login.place.email")} className="w100" onChange={e => updateStatus({ email: e.target.value })} />
								<button className='btn-yo' style={{ position: 'absolute', right: '10px', top: '12%' }} onClick={() => { submit() }}>{T("title.subscribe")}</button>
							</div>
							<div className="col-3"></div>
						</div>
					</ScrollAnimation>
				</div>
			</div>
			<Footer />
			<SideMenu menushow={state.showSideMenu} onClose={() => updateStatus({ showSideMenu: false })} />
			<div>
				<img src={ellipse1} className="ellipse ellipse1" alt='' />
				<img src={ellipse3} className="ellipse ellipse3" alt='' />
				<img src={ellipse2} className="ellipse ellipse2" alt='' />
			</div>
		</div>
	)
};

export default Infrastructure;