import React from 'react'
import { Link } from 'react-router-dom'
import ScrollAnimation from 'react-animate-on-scroll';

import '../../assets/css/landing.css'
import '../../assets/css/animate.min.css'

import SelectInput from '../../components/SelectInput'
import useStore from '../../useStore'
import { tips, validateEmail, validateUsername } from '../../util'
import Footer from '../Layout/footer'
import SideMenu from '../Layout/sidemenu'


import logo from '../../assets/img/logo_white.svg'
import menu from '../../assets/img/menu.webp'
import phone from '../../assets/img/phone.svg'
import password from '../../assets/img/password.webp'
import yo from '../../assets/img/yo.webp'
import sec2_1 from '../../assets/img/icon_landing_1.svg'
import sec2_2 from '../../assets/img/icon_landing_2.svg'
import sec2_3 from '../../assets/img/icon_landing_3.svg'
import landing_client_1 from '../../assets/img/LifeLongProgram.svg'
import landing_client_2 from '../../assets/img/Mayor-of-London.svg' 
import landing_client_3 from '../../assets/img/NHS.svg'
import landing_client_4 from '../../assets/img/Circulation_Fou.svg'
import landing_client_5 from '../../assets/img/double-helix.svg' 
import work1 from '../../assets/img/landing-work-1.webp'
import work2 from '../../assets/img/landing-work-2.webp' 
import work3 from '../../assets/img/landing-work-3.webp'
import ellipse1 from '../../assets/img/Ellipse_1.webp' 
import ellipse2 from '../../assets/img/Ellipse_2.webp'
import ellipse3 from '../../assets/img/Ellipse_3.webp'
import ellipse4 from '../../assets/img/Ellipse_4.webp'
import landing_1 from '../../assets/img/landing_1.webp'
import landing_2 from '../../assets/img/landing_2.webp'

interface LandingType {
	headerBack: string
	name: string
	email: string
	checkbox1: boolean
	checkbox2: boolean
	checkbox3: boolean
	showSideMenu: boolean
}
const Landing = () => {
	const [state, setStates] = React.useState<LandingType>({
		headerBack: '',
		name: '',
		email: '',
		checkbox1: false,
		checkbox2: false,
		checkbox3: false,
		showSideMenu: false
	})
	const { call, update, T, changeLang, lang } = useStore()        
	const updateStatus = (params: Partial<LandingType>) => setStates({ ...state, ...params })

	const submit = async () => {
		const { email, name } = state
		if (email === '' || !validateEmail(email)) {
			tips("Invalid e-mail")
			return
		} else if (name === '' || !validateUsername(name)) {
			tips("Invalid name")
			return
		}
		update({ loading: true })
		const res = await call("login", { email, password });
		// if (res) {
		tips("Success"+res)
		// }
		// update({ loading: false })
		setTimeout(() => {
			update({ loading: false })
		}, 2000);
	}
	
	React.useEffect(() => {
		const onHandler = (ev) => {
			if (window.scrollY > 100) {
				setStates({ ...state, headerBack: 'rgba(0,0,0,0.7)' })
			} else {
				setStates({ ...state, headerBack: 'transparent' })
			}
		}
		window.addEventListener('scroll', onHandler);
		return () => window.removeEventListener('scroll', onHandler)
	})
	return (
		<div className="landing">
			<div className="header-panel" style={{ backgroundColor: state.headerBack }}>
				<div className="container">
					<header>
						<div className='justify' >
							<div className='justify'>
								<Link to="/"><img src={logo} className="logo " alt={'logo'} /></Link>
								<div style={{ width: '140px' }} >
									<SelectInput name="Select State" type="language" valueIndex={lang} items={["English", "Arabic", "Chinese"]} onValueChange={(value) => { changeLang(value) }} />
								</div>
							</div>
							<div className='header-menu-bar ml5'>
								<Link to="/solutions" className="header-menu"><span></span>{T("menu.solutions")}</Link>
								<Link to="/infrastructure" className="header-menu "><span></span>{T("menu.infrastructure")}</Link>
								<Link to="/hosting" className="header-menu"><span></span>{T("menu.hosting")}</Link>
								<Link to="/contactus" className="header-menu"><span></span>{T("menu.support")}</Link>
							</div>
							<div></div>
							<div className='justify header-right-menu-bar' >
								<a href="tel:08002289090" className='header-phone justify' >
									<img src={phone} className="mr2  header-menu-icon" alt={'phone'} />
									<h4 className='text-light'>800 228 9090</h4>
								</a>
								<Link to="/login" className='btn-login btn-yo'>{T("login.btn.login")}</Link>
								<Link to="/login"><img src={password} className="btn-login header-menu-icon" alt={'login'} /></Link>
								<span style={{ cursor: 'pointer' }} onClick={() => { updateStatus({ showSideMenu: true }) }}><img src={menu} className="header-menu-icon" alt={'menu'} /></span>
							</div>
						</div>
					</header>
				</div>
			</div>
			<div className="section-1 flex">
				<img src={yo} alt="yo" className='img-yo'></img>
				<div className="container flex">
					<div className="row middle" style={{ alignSelf: 'center' }}>
						<div className="col-xl-7 col-md-12">
							<div className='ml-auto mr-auto mb3'>
								<ScrollAnimation animateIn="slideInUp" animateOut="fadeOutUp">
									<h1>{T("landing.title1")}</h1>
								</ScrollAnimation>
								<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
									<h1>{T("landing.title2")}</h1>
								</ScrollAnimation>
								<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
									<h2>{T("landing.title3")}</h2>
								</ScrollAnimation>
								<ScrollAnimation animateIn="bounceInLeft" animateOut="fadeOutUp">
									<button className='btn-transparent'>{T("landing.cancel")}</button>
								</ScrollAnimation>
							</div>
						</div>
						<div className="col-xl-5 col-md-12 center">
							<ScrollAnimation animateIn="fadeIn" animateOut="fadeOutUp">
								<div className='ask-panel ml-auto mr-auto'>
									<h3 className='text-primary'>{T("landing.challenge.title")}</h3>
									<hr />
									<p>{T("landing.challenge.desc")}</p>
									<div className='radio-label' onClick={() => updateStatus({ checkbox1: !state.checkbox1 })}><span className={`radio ${state.checkbox1 ? "checked" : ""}`}></span>{T("landing.challenge.check1")}</div>
									<div className='radio-label' onClick={() => updateStatus({ checkbox2: !state.checkbox2 })}><span className={`radio ${state.checkbox2 ? "checked" : ""}`}></span>{T("landing.challenge.check2")}</div>
									<div className='radio-label' onClick={() => updateStatus({ checkbox3: !state.checkbox3 })}><span className={`radio ${state.checkbox3 ? "checked" : ""}`}></span>{T("landing.challenge.check3")}</div>
									<div className='mt2'>
										<input type="text" onChange={e => updateStatus({ name: e.target.value })} placeholder='Enter your full name' className='w100 mt3' />
										<input type="text" onChange={e => updateStatus({ email: e.target.value })} placeholder='Enter your e-mail address' className='w100' />
									</div>
									<div className="row mt3 center">
										<button className='btn-yo ml1' onClick={() => { submit() }}> {T("landing.submit")}</button>
										<button className='btn-white'>{T("landing.cancel")}</button>
									</div>
								</div>
							</ScrollAnimation>
						</div>
					</div>
				</div>
			</div>
			<div className="section-2">
				<div className="des-panel ml5 container">
					<div className="row">
						<div className="col-lg-4 col-md-12 m0 p0">
							<ScrollAnimation animateIn="slideInUp" animateOut="fadeOutUp">
								<Link to="/" className="des-item text-light">
									<img src={sec2_2} alt="landing-sec2-img" />
									<div>
										<h4 className='text-primary'>{T("landing.sec2.title1")}</h4>
										<p >{T("landing.sec2.desc1")}</p>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-4 col-md-12 m0 p0">
							<ScrollAnimation animateIn="slideInUp" delay={300} animateOut="fadeOutUp">
								<Link to="/" className="des-item text-light">
									<img src={sec2_1} alt="landing-sec2-img" />
									<div>
										<h4 className='text-primary'>{T("landing.sec2.title2")}</h4>
										<p >{T("landing.sec2.desc2")}</p>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-4 col-md-12 m0 p0">
							<ScrollAnimation animateIn="slideInUp" delay={500} animateOut="fadeOutUp">
								<Link to="/" className="des-item text-light">
									<img src={sec2_3} alt="landing-sec2-img" />
									<div>
										<h4 className='text-primary'>{T("landing.sec2.title3")}</h4>
										<p >{T("landing.sec2.desc3")}</p>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
					</div>
				</div>
			</div>
			<div className="section-3">
				<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
					<div className="row">
						<div className="col-12" style={{ position: 'relative' }}>
							<h3 className='h2 text-stroke'>{T("landing.whatdo.title")}</h3>
							<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("landing.whatdo.title")}</h3>
						</div>
					</div>
				</ScrollAnimation>
				<div className="row center ">
					<div className="col-12">
						<ScrollAnimation animateIn="fadeInUp" delay={100} animateOut="fadeOutUp">
							<h4 className='text-light m0 p0 text-center' >{T("landing.whatdo.desc")}</h4>
						</ScrollAnimation>
					</div>
				</div>
				<div className="container">
					<div className="row center mt2">
						<div className="col-lg-4 col-md-6">
							<ScrollAnimation animateIn="flipInX" delay={100} animateOut="fadeOutUp">
								<Link to="/solutions" >
									<div className="work-card">
										<div className="row center">
											<img src={work1} style={{ maxHeight: '56px', width: '40px', height: '56px' }} alt="work-img-1" />
										</div>
										<h4>{T("landing.whatdo.card.title1")}</h4>
										<p>{T("landing.whatdo.card.desc1")}</p>
									</div>
								</Link>
							</ScrollAnimation>
						</div>

						<div className="col-lg-4 col-md-6">
							<ScrollAnimation animateIn="flipInX" delay={300} animateOut="fadeOutUp">
								<Link to="/solutions" >
									<div className="work-card">
										<div className="row center">
											<img src={work2} style={{ maxHeight: '55px', width: '55px', height: '100px' }} alt="work-img-1" />
										</div>
										<h4>{T("landing.whatdo.card.title2")}</h4>
										<p>{T("landing.whatdo.card.desc2")}</p>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-4 col-md-6">
							<ScrollAnimation animateIn="flipInX" delay={500} animateOut="fadeOutUp">
								<Link to="/solutions" >
									<div className="work-card">
										<div className="row center">
											<img src={work3} style={{ maxHeight: '55px', width: '55px', height: '100px' }} alt="work-img-1" />
										</div>
										<h4>{T("landing.whatdo.card.title3")}</h4>
										<p>{T("landing.whatdo.card.desc3")}</p>
									</div>
								</Link>
							</ScrollAnimation>
						</div>
					</div>
				</div>

			</div>
			<div className="section-4">
				<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
					<div className="row">
						<div className="col-12" style={{ position: 'relative' }}>
							<h3 className='h2 text-stroke' style={{ left: 'calc(50% - 5em)' }}>{T("landing.whychoose.title")}</h3>
							<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("landing.whychoose.title")}</h3>
						</div>
					</div>
				</ScrollAnimation>
				<div className="row center ">
					<div className="col-12">
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<h4 className='text-light m0 p0 text-center' >{T("landing.whychoose.desc")}</h4>
						</ScrollAnimation>
					</div>
				</div>

				<div className="container">
					<div className="row mt5 center">
						<div className="col-xl-6 col-lg-12 row round-desc-1" style={{ alignItems: 'center', transform: 'translateX(200px)', zIndex: 100, justifyContent: 'center' }}>
							<ScrollAnimation animateIn="slideInLeft" animateOut="fadeOutUp">
								<h1 className='stroke-number'>1</h1>
							</ScrollAnimation>
							<div className="">
								<h3>{T("landing.whychoose.card.title1")}</h3>
								<p>{T("landing.whychoose.card.desc1")}</p>
							</div>
						</div>
						<div className="col-xl-6 col-lg-12">
							<ScrollAnimation animateIn="rotateIn" animateOut="fadeOutUp">
								<div className="choose-image-pan">
									<img src={landing_1} alt="landing_1" className='landing-choose-image' />
								</div>
							</ScrollAnimation>
						</div>
					</div>
					<div className="row mt5 center">
						<div className="col-xl-6 col-lg-12">
							<ScrollAnimation animateIn="rotateIn" animateOut="fadeOutUp">
								<div className="choose-image-pan" style={{ transform: 'translateX(200px)', }} >
									<img src={landing_2} alt="landing_2" className='landing-choose-image' />
								</div>
							</ScrollAnimation>
						</div>
						<div className="col-xl-6 col-lg-12 row round-desc-1" style={{ alignItems: 'center', transform: 'translateX(-80px)', zIndex: 100, justifyContent: 'center' }}>
							<div className="round-desc-2">
								<h3>{T("landing.whychoose.card.title2")}</h3>
								<p>{T("landing.whychoose.card.desc2")}</p>
							</div>
							<ScrollAnimation animateIn="slideInRight" animateOut="fadeOutUp">
								<h1 className='stroke-number'>2</h1>
							</ScrollAnimation>
						</div>
					</div>
				</div>
			</div>
			<div className="section-5">
				<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
					<div className="row">
						<div className="col-12" style={{ position: 'relative' }}>
							<h2 className='h2 text-stroke' >{T("title.ourclient")}</h2>
							<h2 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("title.ourclient")}</h2>
						</div>
					</div>
				</ScrollAnimation>
				<div className="container">
					<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
						<div className="row center">
							<h4 className='text-light   text-center' style={{ width: '80%' }}>{T("desc.ourclient")}</h4>
						</div>
					</ScrollAnimation>
				</div>
				<div className="container mt3">
					<div className="row center mt5 mb5">
						<div className="col-lg-2 col-md-4 text-center">
							<ScrollAnimation animateIn="fadeInUp" delay={100} animateOut="fadeOutUp">
								<Link to="/contactus"><img src={landing_client_1} alt="client-icon" className='client-link' /></Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-2 col-md-4 text-center">
							<ScrollAnimation animateIn="fadeInUp" delay={400} animateOut="fadeOutUp">
								<Link to="/contactus"><img src={landing_client_2} alt="client-icon" className='client-link' /></Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-2 col-md-4 text-center">
							<ScrollAnimation animateIn="fadeInUp" delay={600} animateOut="fadeOutUp">
								<Link to="/contactus"><img src={landing_client_3} alt="client-icon" className='client-link' /></Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-2 col-md-4 text-center">
							<ScrollAnimation animateIn="fadeInUp" delay={800} animateOut="fadeOutUp">
								<Link to="/contactus"><img src={landing_client_4} alt="client-icon" className='client-link' /></Link>
							</ScrollAnimation>
						</div>
						<div className="col-lg-2 col-md-4 text-center">
							<ScrollAnimation animateIn="fadeInUp" delay={1000} animateOut="fadeOutUp">
								<Link to="/contactus"><img src={landing_client_5} alt="client-icon" className='client-link' /></Link>
							</ScrollAnimation>
						</div>
					</div>
				</div>
			</div>
			<Footer />
			<SideMenu menushow={state.showSideMenu} onClose={() => updateStatus({ showSideMenu: false })} />
			<div>
				<img src={ellipse1} className="ellipse ellipse1" alt='' />
				<img src={ellipse3} className="ellipse ellipse3" alt='' />
				<img src={ellipse4} className="ellipse ellipse4" alt='' />
				<img src={ellipse2} className="ellipse ellipse2" alt='' />
			</div>
		</div>
	)
};

export default Landing;