import React from 'react'
import { Link } from 'react-router-dom'
import ScrollAnimation from 'react-animate-on-scroll';

import '../../assets/css/animate.min.css'
import '../../assets/css/solutions.css'

import Footer from '../Layout/footer'
import useStore from '../../useStore'
import { tips, validateEmail } from '../../util'
import SelectInput from '../../components/SelectInput'
import SideMenu from '../Layout/sidemenu'

import logo from '../../assets/img/logo_white.svg'
import menu from '../../assets/img/menu.webp'
import phone from '../../assets/img/phone.svg'
import password from '../../assets/img/password.webp'
import home from '../../assets/img/home.webp';
import arrow_left from '../../assets/img/arrow_left.svg'
import rocket from '../../assets/img/rocket.svg';
import mixcloud from '../../assets/img/mixcloud.svg';
import mobile from '../../assets/img/mobile.svg';
import basket from '../../assets/img/basket.svg';
import desktop from '../../assets/img/desktop.svg';
import header_back from '../../assets/img/header_back.webp';

import ellipse1 from '../../assets/img/Ellipse_1.webp'
import ellipse2 from '../../assets/img/Ellipse_2.webp'
import ellipse3 from '../../assets/img/Ellipse_3.webp'

interface MsgType {
	email: string
	headerBack: string
	showSideMenu: boolean
}

const Solutions = () => {
	const [state, setStates] = React.useState<MsgType>({
		email: '',
		headerBack: 'transparent',
		showSideMenu: false
	})
	const {  update, T, changeLang, lang } = useStore()
	const updateStatus = (params: { [key: string]: string | number | boolean }) => setStates({ ...state, ...params })

	React.useEffect(() => {
		const onHandler = (ev) => {
			if (window.scrollY > 100) {
				setStates({ ...state, headerBack: 'rgba(0,0,0,0.7)' })
			} else {
				setStates({ ...state, headerBack: 'transparent' })
			}
		}
		window.addEventListener('scroll', onHandler);

		return () => window.removeEventListener('scroll', onHandler)
	})

	const submit = async () => {
		const { email } = state
		if (email === '' || !validateEmail(email)) {
			tips("Invalid e-mail")
			return
		}
		update({ loading: true })
		// const res = await call("submit", { email, password });
		// if (res) {
		tips("Success")
		// }
		// update({ loading: false })
		setTimeout(() => {
			update({ loading: false })
		}, 2000);
	}




	return (
		<div className="solutions">
			<div className="header-panel" style={{ backgroundColor: 'rgba(0,0,0,0.7)', position: 'relative', backgroundImage: `url(${header_back})` }}>
				<div className="container">
					<header>
						<div className='justify'>
							<div className='justify'>
								<Link to="/"><img src={logo} className="logo " alt={'logo'} /></Link>
								<div style={{ width: '140px' }} >
									<SelectInput name="Select State" type="language" valueIndex={lang} items={["English", "Arabic", "Chinese"]} onValueChange={(value) => { changeLang(value) }} />
								</div>
							</div>
							<div className='header-menu-bar ml5'>
								<Link to="/solutions" className="header-menu active"><span></span>{T("menu.solutions")}</Link>
								<Link to="/infrastructure" className="header-menu "><span></span>{T("menu.infrastructure")}</Link>
								<Link to="/hosting" className="header-menu"><span></span>{T("menu.hosting")}</Link>
								<Link to="/contactus" className="header-menu"><span></span>{T("menu.support")}</Link>
							</div>
							<div></div>
							<div className='justify'>
								<a href="tel:08002289090" className='header-phone justify' >
									<img src={phone} className="mr2  header-menu-icon" alt={'phone'} />
									<h4 className='text-light'>800 228 9090</h4>
								</a>
								<Link to="/login" className='btn-login btn-yo'>{T("login.btn.login")}</Link>
								<Link to="/"><img src={password} className="btn-login header-menu-icon" alt={'login'} /></Link>
								<span style={{ cursor: 'pointer' }} onClick={() => { updateStatus({ showSideMenu: true }) }}><img src={menu} className="header-menu-icon" alt={'menu'} /></span>
							</div>
						</div>
						<hr style={{ borderTop: '1px solid var(--font-black-color)' }} />
					</header>
					<div className="container">
						<div className="row">
							<div className="col-12 ">
								<h3 className='text-stroke h2 title' style={{ zIndex: 0, WebkitTextStroke: '1px rgba(255,255,255,0.1)' }}>{T("menu.solutions")}</h3>
								<h1 className='text-light h1 title' style={{ zIndex: 2 }}>{T("menu.solutions")}</h1>
							</div>
						</div>
						<div className="row m0 pb3">
							<div className="col-12" style={{ fontSize: '24px' }}>
								<Link to="/"><img src={home} style={{ width: '20px' }} alt={'home'} /></Link>
								<label className='text-light'> / </label>
								<Link to="/solutions"><label className='text-primary'> {T("menu.solutions")} </label></Link>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div className="section-1">
				<div className="container">
					<div className="row mt3" style={{ alignItems: 'center' }}>
						<div className="col-xl-7 col-md-12">
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<div className="row center">
									<h2 className='text-light p3' style={{ width: '80%' }}>{T("solutions.desc1")}</h2>
								</div>
							</ScrollAnimation>
						</div>
						<div className="col-xl-5 col-md-12">
							<div className="row center">
								<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
									<div className="row"><h5>{T("solutions.desc2")}</h5></div>
								</ScrollAnimation>
							</div>
							<div className="row center">
								<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
									<a href="/contactus" className='btn-transparent'>{T("btn.contact")}</a>
								</ScrollAnimation>
							</div>

						</div>
					</div>
					<ScrollAnimation animateIn="fadeIn" animateOut="fadeOutUp">
						<div className="row  mt3">
							<div className="col-lg-4 col-md-6">
								<Link to="solution-details/mobile-app" className="soft-card">
									<div className="row">
										<div className="col-12">
											<img src={mobile} alt="desktop" />
											<h3>{T("solutions.card.title1")}</h3>
											<div className='h4'>{T("title.learn_more")}<img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link>
							</div>
							<div className="col-lg-4 col-md-6">
								<Link to="solution-details/cloud" className="soft-card ">
									<div className="row">
										<div className="col-12">
											<img src={mixcloud} alt="desktop" />
											<h3><br></br>{T("solutions.card.title2")}</h3>
											<div className='h4'>{T("title.learn_more")} <img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link>
							</div>
							<div className="col-lg-4 col-md-6">
								<Link to="solution-details/ecommerce" className="soft-card">
									<div className="row">
										<div className="col-12">
											<img src={basket} alt="rocket" />
											<h3>{T("solutions.card.title3")}</h3>
											<div className='h4'>{T("title.learn_more")} <img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link>
							</div>
							<div className="col-lg-4 col-md-6">
								<Link to="solution-details/data" className="soft-card row2">
									<div className="row">
										<div className="col-12">
											<img src={rocket} alt="basket" />
											<h3>{T("solutions.card.title4")}</h3>
											<div className='h4'>{T("title.learn_more")} <img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link> 
							</div>
							<div className="col-lg-4 col-md-6">
								<Link to="solution-details/virtual-reality" className="soft-card row2">
									<div className="row">
										<div className="col-12">
											<img src={desktop} alt="mixcloud" />
											<h3>{T("solutions.card.title5")}</h3>
											<div className='h4'>{T("title.learn_more")} <img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link>
							</div>
							<div className="col-lg-4 col-md-6">
								<Link to="solution-details/decentralize" className="soft-card row2">
									<div className="row">
										<div className="col-12">
											<img src={rocket} alt="basket" />
											<h3>{T("solutions.card.title6")}</h3>
											<div className='h4'>{T("title.learn_more")} <img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link> 
							</div>
							{/* <div className="col-lg-4 col-md-6">
								<Link to="solution-details/iot" className="soft-card ">
									<div className="row">
										<div className="col-12">
											<img src={desktop} alt="mobile" />
											<h3>{T("solutions.card.title6")}</h3>
											<div className='h4'>{T("title.learn_more")} <img src={arrow_left} style={{ transform: 'rotate(180deg) translateY(-10px)' }} alt="arrow-left" /></div>
										</div>
									</div>
								</Link>
							</div> */}
						</div>
					</ScrollAnimation>
					<div className="row mt2 center">
						<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
							<button className='btn-transparent'>{T("btn.load_more")}</button>
						</ScrollAnimation>
					</div>
				</div>
			</div>
			<div className="section-2">
				<div className="container">
					<div className="row">
						<div className="col-12" style={{ position: 'relative' }}>
							<h3 className='h2 text-stroke' style={{ left: 'calc(50% - 8rem)' }}>{T("title.newsletter")}</h3>
							<ScrollAnimation animateIn="fadeInUp" animateOut="fadeOutUp">
								<h3 className='text-primary h2 text-center' style={{ zIndex: 2 }}>{T("title.newsletter")}</h3>
							</ScrollAnimation>
						</div>
					</div>
					<div className="row center">
						<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
							<h3>{T("solutions.newsletter.desc")}</h3>
						</ScrollAnimation>
					</div>
					<ScrollAnimation animateIn="flipInX" animateOut="fadeOutUp">
						<div className="row center">
							<div className="col-3"></div>
							<div className="col-xl-6 col-md-12 row">
								<input type="text" placeholder={T("login.place.email")} className="w100" onChange={e => updateStatus({ email: e.target.value })} />
								<button className='btn-yo' style={{ position: 'absolute', right: '10px', top: '12%' }} onClick={() => { submit() }}>{T("title.subscribe")}</button>
							</div>
							<div className="col-3"></div>
						</div>
					</ScrollAnimation>
				</div>
			</div>
			<Footer />
			<SideMenu menushow={state.showSideMenu} onClose={() => updateStatus({ showSideMenu: false })} />
			<div>
				<img src={ellipse1} className="ellipse ellipse1" alt='' />
				<img src={ellipse3} className="ellipse ellipse3" alt='' />
				<img src={ellipse2} className="ellipse ellipse2" alt='' />
			</div>
		</div >
	)
};

export default Solutions;